[type="radio"]:checked,
[type="radio"]:not(:checked) {
  position: absolute;
  left: -9999px; }

[type="radio"]:checked + label,
[type="radio"]:not(:checked) + label {
  position: relative;
  cursor: pointer;
  line-height: 20px;
  display: inline-block;
  color: #666; }

[type="radio"]:checked + label:before,
[type="radio"]:not(:checked) + label:before {
  content: '';
  position: absolute;
  width: 56px;
  height: 56px;
  border-style: solid;
  border-width: 1px;
  border-color: #e5ddd5;
  border-radius: 50%;
  background-color: white; }

[type="radio"]:checked + label:active:before {
  background-color: #d5c9bd; }

[type="radio"]:checked + label:active:checked {
  background-color: #d5c9bd; }

[type="radio"]:checked + label:after,
[type="radio"]:not(:checked) + label:after {
  content: '';
  border-style: solid;
  border-width: 1px;
  border-color: #e5ddd5;
  border-radius: 50%;
  background-color: white;
  position: absolute;
  width: 22px;
  height: 22px;
  top: 30%;
  left: 30%; }

[type="radio"]:not(:checked) + label:after {
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0); }

[type="radio"]:checked + label:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1); }

.form__button {
