  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.form__container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0; }

.form__checkmark {
  position: absolute;
  top: 0;
  left: 0;
  width: 42px;
  height: 42px;
  background-color: #fff;
  border: 1px solid #e5ddd5; }
  .form__checkmark:active {
    background-color: #d5c9bd; }
  .form__checkmark:checked {
    background-color: #d5c9bd; }

.form__checkmark:after {
  content: "";
  position: absolute;
  display: none; }

.form__container input:checked ~ .form__checkmark:after {
  display: block; }

.form__container .form__checkmark:after {
  left: 14px;
  top: 5px;
  width: 10px;
  height: 20px;
  border: solid black;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg); }

